.App {
  text-align: center;
}

body {
  background-color: #0B1D51;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
}

.auth {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #725CAD;
  justify-content: center;
  height: 400px;
  width: 400px;
  border-radius: 25px;
  box-shadow: 10px 10px;
}

.heading {
  display: flex;
  color: #8CCDEB;
  justify-content: center;
  font-family: 'Poppins', 'sans-serif';
  font-weight: 700;
}

.input {
  border-radius: 10px;
  height: 23px;
  width: 38vh;
  background-color: #FFE3A9;
  font-family: 'Poppins';
}

.btn {
  background-color: #FFE3A9;
  height: 30px;
  width: auto;
  border-radius: 10px;
  font-family: 'Poppins';
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.input {
  border: none;
  outline: none;
  border-radius: 15px;
  padding: 0.7em;
  background-color: #ccc;
  box-shadow: inset 2px 5px 10px rgba(0, 0, 0, 0.3);
  transition: 300ms ease-in-out;
}

.input:focus {
  background-color: white;
  transform: scale(1.05);
  box-shadow: 13px 13px 100px #969696,
              -13px -13px 100px #ffffff;
}

.Sign_up {
  display: grid;
  gap: 10px;
}

.Login {
  display: grid;
  gap: 10px;
  grid-template-rows: 0.4fr 0.4fr 0.6fr;
}

/* Already present responsive rule for very small screens */
@media (max-width: 500px) {
  .auth {
    width: 98vw;
    min-width: unset;
    max-width: 98vw;
    height: auto;
    padding: 1rem 0.5rem;
    box-sizing: border-box;
  }

  .input {
    width: 90vw;
    max-width: 98vw;
    min-width: unset;
  }
}

/* âœ… New responsive enhancement (no style change) */
@media (max-width: 768px) {
  body {
    flex-direction: column;
    padding: 1rem;
    box-sizing: border-box;
  }

  .auth {
    width: 90vw;
    height: auto;
    padding: 2rem 1rem;
  }

  .input {
    width: 80vw;
  }

  .btn {
    width: 50vw;
    min-width: 150px;
  }

  .heading {
    font-size: 1.5rem;
    text-align: center;
  }
}
